import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RegisterComponent } from './register/register.component';
import { AutomobileComponent } from './automobile/automobile.component';
import { MaintenanceComponent } from './maintenance/maintenance.component';
import { StabilizerComponent } from './stabilizer/stabilizer.component';
import { BatteryComponent } from './battery/battery.component';
import { InvertorComponent } from './invertor/invertor.component';
import { SolarComponent } from './solar/solar.component';


const routes: Routes = [
  { path:'register',component: RegisterComponent },
  { path:'automobile',component: AutomobileComponent },
  { path:'maintenance',component:MaintenanceComponent },
  { path:'stabilizer', component:StabilizerComponent },
  { path:'battery', component:BatteryComponent },
  { path:'inverter', component:InvertorComponent},
  { path:'solar', component:SolarComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
